<template>
  <div class="my_answer">
    <div class="answer_info">
      <span>我的答案：</span>
      <span
        v-if="answerType === 1"
        :class="myAnswer === trueAnswer ? 'answer_sucess' : ''"
      >{{myAnswer||"未作答"}}</span>
      <img v-else :src="getImageFile(myAnswer)" />
    </div>
    <div class="answer_info" v-if="trueAnswer || getScore">
      <span>正确答案：</span>
      <span class="answer_true">{{trueAnswer}}</span>
    </div>
    <div class="answer_info" v-if="(trueAnswer || getScore) && analysis">
      <span>答案解析：</span>
      <span class="answer_analysis">{{analysis}}</span>
    </div>
    <div class="answer_info" v-if="trueAnswer || comment || getScore">
      <span>我的得分：</span>
      <span class="answer_score">{{getScore||0}}</span> 分
    </div>
    <div class="answer_info" v-if="comment">
      <span>老师点评：</span>
      <span class="answer_analysis">{{comment}}</span>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    questionType: {
      type: Number,
      default: 1
    },
    myAnswer: {
      type: String,
      default: ""
    },
    trueAnswer: {
      // 是否已作答
      type: String,
      default: ""
    },
    getScore: {
      type: Number,
      default: 0
    },
    answerType: {
      type: Number,
      default: 1
    },
    modulePicture: {
      type: Number,
      default: 1
    },
    analysis: {
      type: String,
      default: ""
    },
    comment: {
      type: String,
      default: ""
    }
  },
  data() {
    return {};
  },
  methods: {
    getImageFile(fileId) {
      return this.$gatewayHttp.resourse.picture(fileId, this.modulePicture);
    }
  }
};
</script>

<style lang='less' scoped>
.my_answer {
  width: 92vw;
  padding: 2vh 4vw;
  text-align: left;
  background: rgba(255, 255, 255, 1);
  margin-bottom: 2vh;

  .answer_info {
    font-size: 1.4rem;
    line-height: 1.5;
    margin: 1vh 0;
    word-break: break-all;
    span:last-child {
      font-size: 1.5rem;
    }
    .answer_sucess {
      color: #07c160;
    }
    .answer_true {
      color: rgb(241, 45, 45);
    }
    .answer_analysis {
      color: #07c160;
    }
    .answer_score {
      color: #f44;
    }
    img {
      max-width: 92vw;
      max-height: 30vh;
    }
  }
}
</style>


